
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'


const routerMap = ['course', 'goods']

const bannerList = [{
    img: 'https://static.vux.li/demo/1.jpg',
}, {
    img: 'https://static.vux.li/demo/2.jpg',
}, {
    img: 'https://static.vux.li/demo/3.jpg',
}]

export default {
    name: 'shop',
    data() {
        return {
            bannerList,
            index: 0
        }
    },
    methods: {
        tabClick() {
            this.$router.replace({path: routerMap[this.index]});
        }
    },
    components: {
        Swiper,
        SwiperItem,
        Tab, 
        TabItem, 
        Sticky
    }
}



